
import './App.css';
import lajoyalogo from './Assets/lajoya.png';
import tictactoelogo from './Assets/icon.png';
import background from './Assets/bg2.png'
import { useEffect, useState } from 'react';
import TicTacToe from './TicTacToe';
import Lottie from "lottie-react";
import loading from "./Assets/gaming.json"

export enum Screens {
  WELCOME = 'WELCOME',
  HOME = 'HOME',
  TICTACTOE = 'TICTACTOE',
}

const games = [
  {
    name: 'Infinite TicTacToe',
    logo: tictactoelogo,
    screen: Screens.TICTACTOE,
  },
];

function App() {

  const [currentScreen, setCurrentScreen] = useState<Screens>(Screens.WELCOME);

  useEffect(() => {
    const timer = setTimeout(() => {
      setCurrentScreen(Screens.HOME);
    }, 3000);

    return () => {
      clearTimeout(timer);
    }
  }, [])

  const getCurrentScreenContent = (screen: Screens) => {
    switch (screen) {
      case Screens.WELCOME:
        return (
          <div className="fx-col" style={{ alignItems: 'center', justifyContent: 'center' }}>
            <div className='appName'>Welcome to</div>
            <img src={lajoyalogo} style={
              {
                width: '128px',
                height: '128px',
              }
            } />
            <div className='appName'>LaJoya Games</div>
            <Lottie animationData={loading} style={
              {
                width: '128px',
                height: '128px',
              }
            } loop={true} />
          </div>

        );
      case Screens.HOME:
        return (
          <div className="fx-col" >
            <div className='appName' style={
              {
                width: '100%',
                height: '30px',
                position: 'absolute',
                top: 10,
                left: 0,
              }
            } >Click game to Play !!</div>
            <div className="fx-row fwt" style={{ flexWrap: 'wrap', justifyContent: 'center'}}>
              {games.map(game => (
                <div className="fx-col" style={{width: '50%', justifyContent: 'center', alignItems: 'center'}} onClick={() => setCurrentScreen(game.screen)}>
                <img src={game.logo} style={
                  {
                    width: '128px',
                    height: '128px',
                  }
                } />
                <div className='gameName'>{game.name}</div>
              </div>
              ))}
            </div>

          </div>
        );
      case Screens.TICTACTOE:
        return (<TicTacToe goHome={() => setCurrentScreen(Screens.HOME)} />);
      default:
        return (<div></div>);

    }
  }

  return (
    <div className="App" style={{
      width: '100%',
      height: '100%',
      backgroundImage: `url(${background})`,
      backgroundRepeat: "no-repeat",
      backgroundSize: 'cover',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    }}>

      {getCurrentScreenContent(currentScreen)}

    </div>
  );
}

export default App;
